import React from 'react'
import './Material.css'

class Material extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return(
      <div className="mainMaterial">
        <div className="listMaterial">
          <div className="itemMaterial"><h2>Bygning</h2><h4>{this.props.building.address}</h4></div>
          <div className="itemMaterial"><h2>Bygningsdel</h2><h4>Tag</h4><h4>Ydervægge</h4></div>
        </div>
        <div className="divButton">
          <a className="buttonMaterial">Rediger</a>
          <a className="buttonMaterial">Rediger avanceret</a>
        </div>
      </div>
    )
  }
}
export default Material